.offer {
  padding-top: 135px;
  padding-bottom: 120px;

  @include vp-1199 {
    padding-top: 91px;
    padding-bottom: 101px;
  }

  @include vp-767 {
    padding-top: 63px;
    padding-bottom: 70px;
    text-align: center;
  }

  h2 {
    margin: 0;
    margin-bottom: 34px;
    font-family: $ff-alumni;
    font-size: 54px;
    line-height: normal;
    font-weight: 600;
    text-align: center;
    letter-spacing: 1.62px;

    @include vp-1199 {
      margin-bottom: 40px;
      font-size: 42px;
      letter-spacing: 1.26px;
    }

    @include vp-767 {
      margin-bottom: 42px;
      margin-left: -5px;
      font-size: 32px;
      letter-spacing: 0.96px;
    }
  }
}

.offer__container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, auto);
  gap: 40px;

  @include vp-1199 {
    gap: 30px;
  }

  @include vp-767 {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .offer__button {
    position: absolute;
    width: fit-content;
    margin-top: auto;
    border: 1px solid $color-transparent;

    @include vp-767 {
      position: relative;
      left: unset;
      bottom: unset;
      margin: auto;
      margin-bottom: 0;
    }

    &::after {
      position: absolute;
      width: 67%;
      bottom: 10px;
      border: 1px solid $color-default-white;
      content: "";
    }

    @include hover {
      opacity: 0.8;
    }

    &:focus,
    &:focus-visible {
      border-color: $color-sapphire;
      outline: none;
    }

    &:focus::after,
    &:focus-visible::after {
      display: none;
    }

    &:disabled {
      opacity: 0.3;
    }
  }

  .offer__button--primary {
    &:focus,
    &:focus-visible {
      border-color: $color-default-white;
    }
  }

  img {
    width: 100%;
  }
}

//   

.offer__item:first-of-type {
  grid-column: 1 / 2;
  grid-row: 1 / 3;

  display: grid;
  grid-template-columns: repeat(2, 280px);
  justify-content: space-between;

  color: $color-default-white;
  background-color: $color-torch-red-1;

  @include vp-1199 {
    grid-column: 1 / -1;
    grid-template-columns: 1fr 399px;
  }

  @include vp-767 {
    display: flex;
    flex-direction: column;
  }
}

//      

.offer__description {
  position: relative;
  display: flex;
  flex-direction: column;
  padding: 45px;
  padding-top: 34px;

  @include vp-767 {
    min-height: 299px;
    padding: 34px 45px 32px;
  }

  h3 {
    margin: 0;
    margin-bottom: 45px;
    font-size: 18px;
    line-height: 140%;
    font-weight: 400;

    @include vp-767 {
      margin-bottom: 18px;
    }
  }

  p,
  span {
    margin: 0;
    font-size: 28px;
    font-weight: 700;
    line-height: 156%;
    order: -1;
  }

  p {
    @include vp-1199 {
      line-height: 153%;
    }
  }

  span {
    margin-bottom: 24px;
    font-size: 70px;
    line-height: 86%;

    @include vp-767 {
      margin-bottom: 19px;
    }
  }

  .offer__button {
    left: 24px;
    bottom: 33px;
    font-size: 14px;
    letter-spacing: 0.7px;

    @include vp-767 {
      position: relative;
      left: unset;
      bottom: unset;
      margin: auto;
      margin-bottom: 0;
    }
  }
}

//    

.offer__item:not(:first-of-type) {
  position: relative;
  display: flex;
  flex-direction: column;
  padding: 27px;
  padding-top: 25px;
  color: $color-resolution-blue;
  border-width: 7px;
  border-style: solid;
  border-image: $torch-red-to-cobalt 1;

  @include vp-1199 {
    min-height: 211px;
  }

  @include vp-767 {
    padding-top: 36px;
    padding-bottom: 11px;
  }

  h3 {
    margin: 0;
    margin-bottom: 10px;
    font-size: 26px;
  }

  p {
    margin: 0;
    margin-bottom: 45px;

    @include vp-767 {
      margin-bottom: 28px;
    }
  }

  .offer__button {
    bottom: 17px;
    left: 7px;
    color: $color-resolution-blue;
    letter-spacing: 0.6px;

    @include vp-767 {
      transform: translateX(-7px);
    }

    @include focus {
      border-color: $color-resolution-blue;
    }

    &::after {
      width: 62%;
      border-color: $color-resolution-blue;
    }
  }
}

.offer__item:nth-of-type(2n) {
  p {
    @include vp-1199 {
      padding-right: 40px;
    }

    @include vp-767 {
      padding-right: 0;
    }
  }
}

.offer__item:last-of-type {
  p {
    @include vp-1199 {
      word-spacing: 1.5px;
    }

    @include vp-767 {
      padding: 0 30px;
    }
  }
}
